*{
  margin: 0;
  padding:0;
  box-sizing: border-box;
}

html,
body {
    background-color: rgb(240, 238, 238);
    color: #555;
    font-family: 'Lato', 'Arial', sans-serif;
    font-weight: 300;
    font-size: 20px;
    text-rendering: optimizeLegibility;
    overflow-x: hidden;
}

.row {
  max-width: 1140px;
  margin: 0 auto;
}

